import HttpClient from 'nextcloud-axios';
import Vue from 'vue';
import { generateOcsUrl } from 'nextcloud-server/dist/router';
import Avatar from 'nextcloud-vue/dist/Components/Avatar';
export default {
  name: 'Shares',
  components: {
    Avatar: Avatar
  },
  data: function data() {
    return {
      uniqueShareUIDs: [],
      uidDisplaynameMap: {},
      isLoading: true
    };
  },
  computed: {
    isEmptyList: function isEmptyList() {
      return this.isLoading === false && this.uniqueShareUIDs.length === 0;
    },
    emptyLabel: function emptyLabel() {
      return t('privacy', 'You don\'t have any shares with individual users.');
    }
  },
  mounted: function mounted() {
    var _this = this;

    var url = generateOcsUrl('/apps/files_sharing/api/v1/shares?format=json&shared_with_me=false');
    var currentUserId = OC.getCurrentUser();
    HttpClient.get(url).then(function (resp) {
      resp.data.ocs.data.forEach(function (d) {
        if (d.share_with === currentUserId) {
          return;
        }

        switch (d.share_type) {
          case 0:
            if (_this.uniqueShareUIDs.indexOf(d.share_with) === -1) {
              _this.uniqueShareUIDs.push(d.share_with);

              Vue.set(_this.uidDisplaynameMap, d.share_with, d.share_with_displayname);
            }

            break;

          default:
            break;
        }
      });
      _this.isLoading = false;
    });
  }
};